import {UserType} from "../HW8";

export const SortPeople = 'sort'
export const CheckPeople = 'check'

export interface SortPeopleAction {
    type: typeof SortPeople
    payload: 'up' | 'down'
}

export interface CheckPeopleAction {
    type: typeof CheckPeople,
    payload: number
}


export type HomeWorkActionType =
    SortPeopleAction
    | CheckPeopleAction

export const homeWorkReducer = (state: UserType[], action: HomeWorkActionType): UserType[] => { // need to fix any
    const stateCopy = [...state]
    switch (action.type) {
        case SortPeople: {
            if (action.payload === 'up') return stateCopy.sort((a, b) => a.name.localeCompare(b.name))
            return stateCopy.sort((a, b) => a.name.localeCompare(b.name)).reverse()
        }
        case CheckPeople: {
            return stateCopy.filter((user) => user.age > action.payload)
        }
        default:
            return state
    }
}