export interface LoadingStateType {
    loading: boolean;
}
export type LoadingActionTypes = loadingACT

const initState: LoadingStateType = {
    loading: false,
}



export const loadingReducer = (state = initState, action: LoadingActionTypes): LoadingStateType => { // fix any
    switch (action.type) {
        case TOGGLE_LOADING_TYPE: {
            return {...state, loading: action.data.value}
        }
        default:
            return state
    }
}

export const TOGGLE_LOADING_TYPE = "TOGGLE_LOADING"

export interface loadingACT {
    type: typeof TOGGLE_LOADING_TYPE,
    data: {
        value: boolean
    }
}

export const loadingAC = (value: boolean): loadingACT => {
    return {
        type: TOGGLE_LOADING_TYPE,
        data: {
            value
        }
    }
}