import React, {useState} from 'react'
import SuperEditableSpan from './common/c4-SuperEditableSpan/SuperEditableSpan'
import SuperButton from '../h4/common/c2-SuperButton/SuperButton'
import {restoreState, saveState} from './localStorage/localStorage'
import './Edit.css'

function HW6() {
    const [value, setValue] = useState<string>('')

    const save = () => {
        saveState<string>('editable-span-value', value)
    }
    const restore = () => {
        setValue(restoreState('editable-span-value', 'enter some text here...'))
    }

    return (
        <div>
            <hr/>
            homeworks 6

            {/*should work (должно работать)*/}
            <div className={'edit'}>
                <div className="edit__header">
                    <img className={'edit__img'} src="https://img.icons8.com/ios/50/null/pencil--v1.png"/>
                    <SuperEditableSpan
                        className={'edit__text'}
                        value={value}
                        onChangeText={setValue}
                        spanProps={{children: value ? undefined : 'enter text...'}}
                    />
                </div>
                <div className="edit__btns">
                    <SuperButton onClick={save} className={'edit__btn'}>save</SuperButton>
                    <SuperButton onClick={restore} className={'edit__btn'}>restore</SuperButton>
                </div>
            </div>

            <hr/>
            {/*для личного творчества, могу проверить*/}
            {/*<AlternativeSuperEditableSpan/>*/}
            <hr/>
        </div>
    )
}

export default HW6
