import React from 'react'
import s from './Greeting.module.css'
import SuperInputText from "../h4/common/c1-SuperInputText/SuperInputText";
import SuperButton from "../h4/common/c2-SuperButton/SuperButton";

type GreetingPropsType = {
    name: string;
    setNameCallback: (e: React.ChangeEvent<HTMLInputElement>) => void;
    addUser: () => void;
    error: string;
    totalUsers: number;
    lastUserName: string;
}

// презентационная компонента (для верстальщика)
const Greeting: React.FC<GreetingPropsType> = (
    {name, setNameCallback, addUser, error, totalUsers, lastUserName} // деструктуризация пропсов
) => {
    const inputClass = error ? s.error : ''

    return (
        <div className={s.input__container}>
            <SuperInputText
                value={name}
                onChange={setNameCallback}
                className={`${s.input} ${inputClass}`}
                error={error}
                spanClassName={s.textError}
            />
            <SuperButton onClick={addUser} className={s.button}>
                add
            </SuperButton>
            <span>{totalUsers}</span>
            {lastUserName && (
                <div>Привет, {lastUserName}</div>
            )}

        </div>
    )
}

export default Greeting
